html,
body {
  height: 100%; }

body {
  font-family: "Open Sans", Courier, monospace;
  font-weight: 400;
  font-size: 16px;
  color: #000000;
  margin: 0;
  padding: 0; }

.container {
  max-width: 1440px;
  margin: 0 auto; }

.header {
  width: 100%; }

.navigation {
  max-width: 874px;
  margin: 0 auto; }

.navigation .divider {
  display: block;
  width: 100%;
  height: 1px;
  background-color: rgba(255, 255, 255, 0.2); }

.navigation__logo {
  display: none; }

@media (max-width: 768px) {
  .navigation .divider {
    display: none; }
  .navigation {
    display: flex;
    margin: 0 auto; }
  .navigation__logo {
    display: block;
    margin-top: 25px;
    margin-left: 21px; }
  .navigation {
    justify-content: space-between; }
  .navigation__burger-button {
    margin-left: auto; } }

@media (max-width: 320px) {
  .navigation__logo {
    margin-left: 16px; } }

.navigation-list {
  display: flex;
  list-style-type: none;
  justify-content: space-between;
  margin: 0;
  padding: 21px 18px;
  padding-bottom: 9px; }

.navigation-list__link {
  text-decoration: none;
  font-weight: 300;
  font-size: 16px;
  line-height: 32px;
  letter-spacing: -0.005em;
  font-weight: 300;
  color: rgba(255, 255, 255, 0.7); }

.navigation-list__item .arrow {
  display: none; }

@media (max-width: 768px) {
  .navigation-list__item {
    background-color: #F6F6F6;
    padding: 17px 35px 17px 16px;
    display: flex;
    justify-content: space-between; }
  .navigation-list__item + .navigation-list__item {
    margin-top: 30px; }
  .navigation-list__link {
    font-size: 20px;
    line-height: 140%;
    color: #48494D; } }

.main-page {
  width: 100%; }

.main-page__bg {
  position: absolute;
  z-index: -1;
  left: 50%;
  transform: translateX(-50%);
  top: 0; }

@media (max-width: 768px) {
  .main-page__bg {
    object-fit: cover;
    max-width: 100%;
    height: 340px; } }

@media (max-width: 320px) {
  .main-page__bg {
    height: 390px; } }

.burger-button {
  display: none; }

@media (max-width: 768px) {
  .burger-button {
    width: 50px;
    height: 50px;
    display: block;
    border: none;
    background-color: #0AD9C6;
    cursor: pointer;
    transition: all 0.3s; }
    .burger-button:hover {
      background-color: #83f9ee; } }

.side-menu {
  white-space: nowrap;
  position: relative; }

.side-menu__close {
  position: absolute;
  top: 0;
  right: 0;
  cursor: pointer;
  width: 50px;
  height: 52px;
  background-color: transparent;
  border: none;
  z-index: 100;
  padding: 0;
  display: none; }

.side-menu__close:hover .cross {
  stroke-opacity: 1; }

.side-menu .cross {
  width: 100%;
  height: 100%; }

.main-page--no-js .navigation-list,
.side-menu--open .navigation-list {
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding: 0; }

.main-page--no-js .cross {
  display: none; }

@media (max-width: 768px) {
  .side-menu {
    display: none; }
  .side-menu--open {
    display: flex; }
  .side-menu__close {
    display: block; }
  .main-page--no-js .side-menu,
  .side-menu.side-menu--open {
    position: absolute;
    top: 0;
    right: 0;
    background-color: #ffffff;
    width: 320px;
    height: 568px;
    flex-direction: column;
    justify-content: center; }
  .main-page--no-js .side-menu,
  .side-menu.side-menu--no-js {
    position: static;
    display: flex; }
  .main-page--no-js .burger-button {
    display: none; } }

@media (max-width: 320px) {
  .side-menu.side-menu--open {
    width: 100%; } }
